@keyframes fade-down {
    0% {
      opacity: 0;
      transform: translateY(-30px) scale(0.9)
    }
    100%{
      opacity: 1;
      transform: translateY(0) scale(1);
    }
  }
  
  .animatedComponent {
    animation: fadeIn 0.5s ease-in-out;
  }

*{
    animation: fade-down 1s;
}